/* stylelint-disable */
/* Checkbox group class */
.ppvx_checkbox-group {
  margin: 0.75rem;
  padding: 0;
  border: none;
  text-align: initial;
}
/* Checkbox base class */
.ppvx_checkbox {
  margin-top: 0.75rem;
  position: relative;
  text-align: initial;
}
/* Checkbox label class */
.ppvx_checkbox__label {
  -moz-osx-font-smoothing: grayscale;
  color: #2c2e2f;
  cursor: pointer;
  display: inline-block;
  padding-left: 2.375rem;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
}
.ppvx_checkbox__label:before {
  border-radius: 0.125rem;
  border: 0.0625rem solid #b7bcbf;
  content: '';
  box-sizing: border-box;
  height: 1.5rem;
  left: 0;
  position: absolute;
  width: 1.5rem;
  font-family: 'PPUI-Icons';
  font-size: 1.125rem;
  line-height: 1.2;
  text-align: center;
}
.ppvx_checkbox__label .ppvx_icon {
  color: #2c2e2f;
  position: relative;
  top: 0.125rem;
  margin-left: 0.375rem;
}
/* Checkbox input class */
.ppvx_checkbox__input {
  cursor: pointer;
  opacity: 0;
  position: absolute;
}
.ppvx_checkbox__input:checked + .ppvx_checkbox__label:before {
  background-color: #0070ba;
  border-color: #0070ba;
  color: #ffffff;
  content: '\e90d';
}
.ppvx_checkbox__input:focus + .ppvx_checkbox__label:before {
  box-shadow: 0 0 0 0.1875rem #009cde;
  outline: none;
}
/* Error style for checkbox */
.ppvx_field--error .ppvx_checkbox__input:checked + .ppvx_checkbox__label:before {
  border-color: #d20000;
}
.ppvx_field--error .ppvx_checkbox__label:before {
  border-color: #d20000;
}
.ppvx_checkbox-group__label {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  display: block;
  color: #687173;
}
.ppvx_checkbox-group__label .ppvx_icon {
  color: #2c2e2f;
  position: relative;
  top: 0.125rem;
  margin-left: 0.5625rem;
}
.ppvx_checkbox__helper-text {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  text-align: initial;
  display: -ms-flexbox;
  display: flex;
  margin: 0.375rem 0 0;
  color: #687173;
  margin-top: 0.125rem;
  padding-left: 2.375rem;
}
.ppvx_checkbox-group__helper-text {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  text-align: initial;
  display: -ms-flexbox;
  display: flex;
  margin: 0.375rem 0 0;
  color: #687173;
}
.ppvx_checkbox-group__error-text {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  text-align: initial;
  display: -ms-flexbox;
  display: flex;
  margin: 0.375rem 0 0;
  margin-top: 0.5rem;
  color: #d20000;
}
.ppvx_checkbox-group__error-text:before {
  content: '\e91a';
}
.ppvx_checkbox-group__error-text:before {
  /* using !important to prevent issues with browser extensions that change fonts */
  font-family: 'PPUI-Icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 1.125rem;
  margin-right: 0.375rem;
  margin-left: initial;
}
/* uber level error */
.ppvx_field--error .ppvx_checkbox-group__label {
  color: #d20000;
}
/* stylelint-enable */
