/* stylelint-disable font-family-no-missing-generic-family-keyword */
.ppvx_alert {
  box-sizing: border-box;
  padding: 0.75rem 2.625rem;
  border-width: 0.0625rem;
  border-style: solid;
  border-radius: 4px;
  color: #2c2e2f;
  position: relative;
  text-align: initial;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  line-height: 1.2;
  border-color: #bfdbee;
  background-color: #f5fbfe;
}
.ppvx_alert:before {
  content: '\e935';
  color: #0070ba;
  /* using !important to prevent issues with browser extensions that change fonts */
  font-family: 'PPUI-Icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 1.5rem;
  position: absolute;
  top: 0.75rem;
  left: 0.75rem;
  display: block;
  font-size: 1.125rem;
  text-align: center;
}
.ppvx_alert__close-btn {
  /* using !important to prevent issues with browser extensions that change fonts */
  font-family: 'PPUI-Icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 1.125rem;
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  width: 1.25rem;
  height: 1.25rem;
  text-align: center;
  background-color: transparent;
  color: #2c2e2f;
  border-radius: 50%;
  border: none;
  padding: 0;
  cursor: pointer;
}
.ppvx_alert__close-btn:hover {
  background-color: #eaeced;
}
.ppvx_alert__close-btn:focus {
  outline: none;
  background-color: #cbd2d6;
}
.ppvx_alert__close-btn:before {
  content: '\e919';
  margin-left: 0.0625rem;
}
/* stylelint-enable font-family-no-missing-generic-family-keyword */
.ppvx_alert--type_attention {
  border-color: #ff9600;
  background-color: #fffbf5;
}
.ppvx_alert--type_attention:before {
  content: '\e947';
  color: #ff9600;
}
.ppvx_alert--type_info {
  border-color: #9da3a6;
  background-color: #f5f7fa;
}
.ppvx_alert--type_info:before {
  content: '\e925';
  color: #2c2e2f;
}
.ppvx_alert--type_success {
  border-color: #299976;
  background-color: #f5fdfb;
}
.ppvx_alert--type_success:before {
  content: '\e90e';
  color: #299976;
}
.ppvx_alert--type_error {
  border-color: #d20000;
  background-color: #fff7f7;
}
.ppvx_alert--type_error:before {
  color: #d20000;
  content: '\e91a';
}
