.ppvx_btn {
  background-color: #0070ba;
  border-radius: 1.5rem;
  border: 0.0625rem solid #0070ba;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  font-weight: 700;
  min-width: 6rem;
  padding: 0.6875rem 1.4375rem;
  text-align: center;
  text-decoration: none;
  transition: color 0.2s ease, background-color 0.2s ease, border-color 0.2s ease, box-shadow 0.2s ease;
}
.ppvx_btn:hover,
.ppvx_btn:focus,
.ppvx_btn:active,
.ppvx_btn:visited {
  color: #ffffff;
}
.ppvx_btn:hover,
.ppvx_btn:focus,
.ppvx_btn:active {
  background-color: #003087;
  border-color: #003087;
}
.ppvx_btn:hover {
  text-decoration: none;
}
.ppvx_btn:focus,
.ppvx_btn:active {
  outline: none;
  text-decoration: underline;
}
.ppvx_btn--inverse {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #0070ba;
}
.ppvx_btn--inverse:visited {
  color: #0070ba;
}
.ppvx_btn--inverse:hover,
.ppvx_btn--inverse:focus,
.ppvx_btn--inverse:active {
  background-color: #f5f7fa;
  border-color: #f5f7fa;
  color: #003087;
}
.ppvx_btn--secondary {
  background: rgba(0, 0, 0, 0);
  border-color: #0070ba;
  color: #0070ba;
}
.ppvx_btn--secondary:visited {
  color: #0070ba;
}
.ppvx_btn--secondary:hover,
.ppvx_btn--secondary:focus,
.ppvx_btn--secondary:active {
  background: rgba(0, 0, 0, 0);
  border-color: #003087;
  box-shadow: 0 0 0 0.0625rem #003087 inset;
  color: #003087;
}
.ppvx_btn--secondary.ppvx_btn--inverse {
  border-color: #ffffff;
  color: #ffffff;
}
.ppvx_btn--secondary.ppvx_btn--inverse:visited {
  color: #ffffff;
}
.ppvx_btn--secondary.ppvx_btn--inverse:hover,
.ppvx_btn--secondary.ppvx_btn--inverse:focus,
.ppvx_btn--secondary.ppvx_btn--inverse:active {
  border-color: #ffffff;
  color: #ffffff;
  box-shadow: 0 0 0 0.0625rem #ffffff inset;
}
.ppvx_btn--icon {
  background: rgba(0, 0, 0, 0);
  border-color: #0070ba;
  color: #0070ba;
  padding-left: 0.5625rem;
}
.ppvx_btn--icon:visited {
  color: #0070ba;
}
.ppvx_btn--icon:hover,
.ppvx_btn--icon:focus,
.ppvx_btn--icon:active {
  background: rgba(0, 0, 0, 0);
  border-color: #003087;
  box-shadow: 0 0 0 0.0625rem #003087 inset;
  color: #003087;
}
.ppvx_btn--icon.ppvx_btn--inverse {
  border-color: #ffffff;
  color: #ffffff;
}
.ppvx_btn--icon.ppvx_btn--inverse:visited {
  color: #ffffff;
}
.ppvx_btn--icon.ppvx_btn--inverse:hover,
.ppvx_btn--icon.ppvx_btn--inverse:focus,
.ppvx_btn--icon.ppvx_btn--inverse:active {
  border-color: #ffffff;
  color: #ffffff;
  box-shadow: 0 0 0 0.0625rem #ffffff inset;
}
.ppvx_btn--icon > span {
  vertical-align: top;
  padding-right: 0.375rem;
  display: inline-block;
}
.ppvx_btn--icon.ppvx_btn--size_md,
.ppvx_btn--icon.ppvx_btn--size_sm {
  padding-left: 0.375rem;
}
/*
  Buttons are large by default
  Class for medium size button
*/
.ppvx_btn--size_md {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  font-weight: 700;
  border-radius: 1.125rem;
  min-width: 4.5rem;
  padding: 0.5rem 1.0625rem;
}
/* Class for small size button */
.ppvx_btn--size_sm {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  font-weight: 700;
  min-width: 3.75rem;
  padding: 0.3125rem 0.875rem;
}
