/*
*   CSS for Tiles
*/
.ppvx_tile {
  background-color: transparent;
  margin-bottom: 1.5rem;
  padding: 1.5rem;
  position: relative;
  text-align: left;
}
@media (min-width: 47rem) {
  .ppvx_tile {
    margin-bottom: 0;
  }
}
.ppvx_tile--card {
  background-color: #ffffff;
  border-radius: 4px;
  border-top: 0.0625rem solid rgba(183, 188, 191, 0.15);
  box-shadow: 0 0.125rem 0.25rem 0 rgba(0, 0, 0, 0.15);
}
.ppvx_tile--divider {
  border-top: 0.0625rem solid #b7bcbf;
}
.ppvx_tile__header {
  margin-bottom: 1.5rem;
  display: table;
}
.ppvx_tile__header-title {
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  font-family: "PayPalSansBig-Medium", Helvetica Neue, Arial, sans-serif;
  width: 100%;
  display: table-cell;
  margin: 0;
  padding: 0.375rem 0;
  text-transform: none;
  vertical-align: middle;
}
.ppvx_tile__content {
  margin-top: 0.9375rem;
  margin-bottom: 0.9375rem;
}
.ppvx_tile__footer {
  margin-top: 1.5rem;
}
.ppvx_tile__footer button,
.ppvx_tile__footer a {
  margin: 0;
}
.ppvx_tile__footer a:not(:last-child),
.ppvx_tile__footer button:not(:last-child) {
  margin-right: 1.5rem;
}
