/* stylelint-disable */
.ppvx_radio-group {
  margin: 0.75rem;
  padding: 0;
  text-align: initial;
  border: none;
}
/* Radio base class */
.ppvx_radio {
  margin-top: 0.75rem;
  position: relative;
  text-align: initial;
}
/* Radio input class */
.ppvx_radio__input {
  cursor: pointer;
  opacity: 0;
  position: absolute;
}
.ppvx_radio__input:checked + .ppvx_radio__label:before {
  border: 6.25rem;
  box-shadow: inset 0 0 0 0.5rem #0070ba;
}
.ppvx_radio__input:focus + .ppvx_radio__label:before {
  box-shadow: inset 0 0 0 0.5rem #0070ba, 0 0 0 0.1875rem #009cde;
  outline: none;
}
/* Radio label class */
.ppvx_radio__label {
  -moz-osx-font-smoothing: grayscale;
  color: #2c2e2f;
  cursor: pointer;
  display: inline-block;
  padding-left: 2.375rem;
  font-size: 0.9375rem;
  line-height: 1.5rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
}
.ppvx_radio__label:before {
  border-radius: 100%;
  border: 0.0625rem solid #b7bcbf;
  content: '';
  height: 1.5rem;
  left: 0;
  position: absolute;
  width: 1.5rem;
}
.ppvx_radio__label .ppvx_icon {
  color: #2c2e2f;
  position: relative;
  top: 0.125rem;
  margin-left: 0.375rem;
}
/* Error style  for radio */
.ppvx_field--error .ppvx_radio__input:checked + .ppvx_radio__label:before {
  border: 0.0625rem solid #d20000;
  box-shadow: inset 0 0 0 0.4375rem #0070ba;
}
.ppvx_field--error .ppvx_radio__input:focus + .ppvx_radio__label:before {
  box-shadow: inset 0 0 0 0.4375rem #0070ba, 0 0 0 0.1875rem #009cde;
  outline: none;
}
.ppvx_field--error .ppvx_radio__label:before {
  border-color: #d20000;
}
.ppvx_radio-group__label {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  display: block;
  color: #687173;
}
.ppvx_radio-group__label .ppvx_icon {
  color: #2c2e2f;
  position: relative;
  top: 0.125rem;
  margin-left: 0.5625rem;
}
.ppvx_radio__helper-text {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  text-align: initial;
  display: -ms-flexbox;
  display: flex;
  margin: 0.375rem 0 0;
  color: #687173;
  margin-top: 0.125rem;
  padding-left: 2.375rem;
}
.ppvx_radio-group__helper-text {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  text-align: initial;
  display: -ms-flexbox;
  display: flex;
  margin: 0.375rem 0 0;
  color: #687173;
}
.ppvx_radio-group__error-text {
  font-size: 0.8125rem;
  line-height: 1.125rem;
  font-family: "PayPalSansSmall-Regular", Helvetica Neue, Arial, sans-serif;
  font-weight: normal;
  text-align: initial;
  display: -ms-flexbox;
  display: flex;
  margin: 0.375rem 0 0;
  margin-top: 0.5rem;
  color: #d20000;
}
.ppvx_radio-group__error-text:before {
  content: '\e91a';
}
.ppvx_radio-group__error-text:before {
  /* using !important to prevent issues with browser extensions that change fonts */
  font-family: 'PPUI-Icons' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 1.125rem;
  margin-right: 0.375rem;
  margin-left: initial;
}
/* uber level error */
.ppvx_field--error .ppvx_radio-group__label {
  color: #d20000;
}
/* stylelint-enable */
